<template>
  <div>
    <common-wrapper class="behavior-list">
      <!-- <el-form :inline="true">
        <el-form-item label="当日信息汇总"></el-form-item>
        <el-form-item label="自动刷新">
          <el-select v-model="autoRefreshTime">
            <el-option
              v-for="(time,index) in autoRefreshSelectOpts"
              :key="index"
              :value="time"
              :label="time >= 60 ? time >= 3600 ? `${time / 3600}小时` : `${time / 60}分钟` : `${time}秒`" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="refreshTime">更新显示</el-button>
        </el-form-item>
      </el-form>
      <progressbar id="progressbar" ref="progressbar" type="Circle" :options="progressbarOpt" :reverse="true" /> -->
      <!-- <div id="progressbar"></div> -->
      <!-- <div class="clear"></div> -->
    <el-row style="margin:10px 0">
        <el-col  :span="4">
          <el-select v-model="warehouseUuid" filterable class="w100p" @change="changeWarehouse">
              <el-option
                v-for="(org, index) in warehouseList"
                :key="index"
                :label="org.warehouseName"
                :value="org.warehouseUuid"></el-option>
            </el-select>
        </el-col>
      </el-row>

      <el-row class="stat-num-container" :gutter="20">
        <el-col  :span="6">
          <div class="stat-num liner-blue">
            <p>待收货</p>
            <p>{{statData.rcvCount}}</p>
          </div>
        </el-col>
        <el-col   :span="6">
          <div class="stat-num liner-lightgreen">
            <p>待上架</p>
            <p>{{statData.upgourntCount}}</p>
          </div>
        </el-col>
        <el-col   :span="6">
          <div class="stat-num liner-orange">
            <p>待下架</p>
            <p>{{statData.underCarriageCount}}</p>
          </div>
        </el-col>
        <el-col   :span="6">
          <div class="stat-num liner-red">
            <p>待发货</p>
            <p>{{statData.deliverCount}}</p>
          </div>
        </el-col>
      </el-row>



      <el-row>
        <el-col  :span="12">
          <common-wrapper class="chart-container">
            <el-row type="flex" align="center" justify="space-between">
              <el-col :span="12">
                <p class="chart-title">待完成任务</p>
              </el-col>
              <el-col :span="12" style="line-height: 73px;text-align: right;">
                <el-radio-group v-model="queryType" @change="queryTypeChange">
                    <el-radio :label="2">最近一周</el-radio>
                    <el-radio :label="1">当日</el-radio>
                </el-radio-group>
               </el-col>
            </el-row>
            <chart-bar
                  :getSeries="barData"
                ></chart-bar>

          </common-wrapper>
        </el-col>
        <el-col  :span="12">
          <common-wrapper class="chart-container">
            <el-row>
              <el-col :span="12">
                <p class="chart-title">不同商家的货物总数</p>
              </el-col>
            </el-row>
            <chart-pie
                  :getSeries="pieData"
                ></chart-pie>
          </common-wrapper>
        </el-col>
      </el-row>
      <el-row>
        <el-col  :span="24">
          <!-- gantt -->
           <common-wrapper class="chart-container">
             <el-row type="flex" align="center" justify="space-between">
               <el-col :span="12" style="line-height: 73px;">
                  <span class="chart-title">货物积压时间</span>
                  <el-select v-model="merchantUuid" filterable clearable  placeholder="请选择商户" @change="changeMerchants">
                          <el-option
                            v-for="(item, index) in merchantsList"
                            :key="index"
                            :label="item.userName"
                            :value="item.userUuid"
                          />
                      </el-select>
               </el-col>
             </el-row>
              <chart-gantt
                  :getSeries="ganttData"
                ></chart-gantt>
          </common-wrapper>
        </el-col>
      </el-row>
    </common-wrapper>
  </div>
</template>

<script>
/**
 * Dashboard Module
 * ----------------------
 * Author: zhou qi
 * Date: 2018.03.20
 */
import moment from 'moment'
import { setSession, getSession } from '@/utils/session'
import { mapGetters } from 'vuex'
import { baseTheme } from '@/utils/theme'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import Progressbar from '@/components/progressbar'
import ChartBar from './Components/charts-bar'
import ChartGantt from './Components/charts-gantt'
import ChartPie from './Components/charts-pie'
import { getToken } from '@/utils/auth.js'

// api
import { getAllMerchantsList } from '@/api/common'
import { getWarehouseList } from '@/api/warehouse'
import { getWarnsCount,getStatData,getBarData,getPieData,getGanttData } from '@/api/dashboard'

export default {
  name: 'Dashboard',
  data () {
    return {
      queryType:2,
      warehouseUuid:'',
      warehouseList:[],
      merchantUuid:'',
      merchantsList:[],
      istableData: false,
      // 自动刷新时间 (单位:秒)
      autoRefreshSelectOpts: [400, 30, 60, 180, 300, 600, 1800, 3600],
      // 当前自动刷新时间间隔
      autoRefreshTime: 10,
      // 自动刷新Interval延迟对象
      refreshInterval: null,
      // 重新激活页面时自动刷新定时器会重启,
      // 这里会使用setTimeout来执行离开页面时保留的剩余时间,
      // 当时间结束后,再重新初始化定时刷新功能
      reActiveTimeout: null,
      // 行为检测列表
      dashTableData: [],
      // 最后更新时间
      lastUpdateTime: '',
      // 设备状态列表分页数据
      deviceStateListPagination: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      // 自动刷新进度条配置项
      progressbarOpt: {
        strokeWidth: 10,
        easing: 'linear',
        duration: 10 * 1000,
        color: '#909399',
        trailColor: '#cbcbcb',
        trailWidth: 1,
        svgStyle: null
      },
      topFiveBrandData: [],
      topFiveBrandName: [],
      statData:{
        waybillNum: "0",
        waitDispatchNum: "0",
        waitPickNum: "0",
        onwayNum: "0",
        finishNum: "0",
        abnormalNum: "0"
      },
      waybillAllList:null,
      waybillStatByAreaList:null,
      transProgressList:null,
      ganttData:[],
      pieData:[],
      barData:[]
    }
  },
  computed: {
    ...mapGetters(['systemTheme']),
    systemColor () {
      return baseTheme[this.systemTheme]
    }
  },
  mounted () {
    // 初始化监测列表
    // this.getDashTableData().then(() => {
    //   // 设置间隔刷新时间
    //   // this.setAutoRefresh(this.autoRefreshTime)
    //   // 设置倒计时
    //   // this.initProgress()
    //   this.topFiveBrandData = [
    //     {
    //       name: '平均配送里程',
    //       type: 'line',
    //       stack: 'km',
    //       data: [103, 77, 75, 64, 64, 63, 60]
    //     },
    //     {
    //       name:'平均安装时间',
    //       type:'line',
    //       stack: 'min',
    //       yAxisIndex:1,
    //       data:[34, 35, 43, 44, 44, 50, 51]
    //     }
    //   ]
    //   this.topFiveBrandName = ['苏A11335','苏AB1251','苏A35443','苏A5B387','苏A156T5']
    // })
    this.warehouseList = this.$store.getters.warehouseList
    if(this.$store.getters.warehouseList.length == 0){
      let token = getToken()
      getWarehouseList({"token":token}).then(res => {
        if(!res.error) {
          this.warehouseList = res
          this.warehouseUuid = getSession('warehouseUuid')
          if(!this.warehouseUuid){
            this.warehouseUuid = res[0].warehouseUuid
            setSession('warehouseUuid',this.warehouseUuid)
          }
          this.initStat()
        }
      })
    }else{
      this.warehouseUuid = getSession('warehouseUuid')
      this.initStat()
    }
  },
  activated () {
    // 重新激活时,根据进度条组件计算剩余时间
    // const remainTime = this.$refs.progressbar.value()
    // 延迟时间即是剩余时间
    // this.reActiveTimeout = setTimeout(() => {
    //   // 定时器会在下一个周期执行自动刷新, 所以本次手动立即执行数据的刷新
    //   this.intervalProcessingData()
    //   // 重启自动刷新
    //   this.setAutoRefresh(this.autoRefreshTime)
    // }, remainTime * this.autoRefreshTime * 1000)
    this.initStat()
  },
  deactivated () {
    // 切换标签页后,需要移除延迟和自动刷新
    // clearTimeout(this.reActiveTimeout)
    // clearInterval(this.refreshInterval)
  },
  methods: {
    // 行为检测列表
    // 这里返回一个Promise以便其后续操作
    getDashTableData () {
      return new Promise(resolve => {
        // getWarnsCount().then(res => {
        //   this.dashTableData = res
          resolve()
        // })
      })
    },
    initStat(){
      let params = {warehouseUuid: getSession('warehouseUuid')}
      this.getBarDataList();
      this.getStatDataList();
      this.getGanttDataList();
      this.getPieDataList();
      this.getMerchantsList(params)
    },
    // 获取商户列表
    getMerchantsList (params) {
      getAllMerchantsList(params).then(res => {
        this.merchantsList = res
      })
    },
    changeWarehouse(val){
      setSession('warehouseUuid',this.warehouseUuid)
      this.merchantUuid = ''
      this.initStat()
    },
    changeMerchants(val){
      if(this.merchantUuid){
        this.getGanttDataList()
      }else{
        this.ganttData = []
      }
    },
    //待完成任务总汇
    getBarDataList(){
      let params = {warehouseUuid:getSession('warehouseUuid'),queryType:this.queryType}
      getBarData(params).then(res=>{
        if(!res.error){
            this.barData = res
        }
      })
    },
    // 不同商家货物总量
    getPieDataList(){
      let params = {warehouseUuid:getSession('warehouseUuid')}
      getPieData(params).then(res=>{
        if(!res.error){
            this.pieData = res
        }
      })
    },
    // 货物积压
    getGanttDataList(){
      let params = {warehouseUuid:getSession('warehouseUuid'),merchantUuid:this.merchantUuid}
      if(this.merchantUuid){
        getGanttData(params).then(res=>{
          if(!res.error){
              this.ganttData = res
          }
        })
      }else{
        this.ganttData = []
      }
    },
    // 统计数字
    getStatDataList(){
      let params = {warehouseUuid:getSession('warehouseUuid')}
      getStatData(params).then(res=>{
        if(!res.error){
            this.statData = res
        }
      })
    },
    queryTypeChange(){
      this.getBarDataList()
    },
    // 翻页事件
    handlePageChange (pageNum) {
      this.deviceStateListPagination.pageNum = pageNum
      const { pageSize } = this.deviceStateListPagination
      this.initDeviceStateList({ pageNum, pageSize })
    },
    // 初始化进度条
    // initProgress () {
    //   this.$refs.progressbar.create()
    // },
    // 重置进度条
    // 每次自动刷新结束后需要重新设置进度条的进度
    restartProgress () {
      this.$refs.progressbar.restart()
    },
    // 这里列举所有需要延迟执行的内容
    // 包括数据获取, 进度条重新初始化等等操作
    intervalProcessingData () {
      // 监测数据
      this.getDashTableData().then(() => {
        this.restartProgress()
      })
    },
    // 自动刷新配置
    // 每隔多久自动刷新数据
    setAutoRefresh (time) {
      if (this.refreshInterval) clearInterval(this.refreshInterval)
      // 重置时间时,需要重新初始化一个Progressbar.
      this.progressbarOpt.duration = time * 1000
      // this.initProgress()
      // 设置延迟间隔执行
      this.refreshInterval = setInterval(this.intervalProcessingData, this.autoRefreshTime * 1000)
    },
    // refresh button handler
    refreshTime () {
      this.setAutoRefresh(this.autoRefreshTime)
    },
    formatTime: function (row, column) {
      return moment(row.updateTime).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  components: { CommonWrapper, CommonTable, Progressbar, ChartBar,ChartPie,ChartGantt }
}
</script>
<style lang="scss" scoped>
.behavior-list {
  position: relative;

  .clear {
    clear: both;
    height:2px;
    overflow: hidden;
    background: $base-light-color;
    margin-bottom:10px;
  }

  #progressbar {
    position: absolute;
    top:12px; right:12px;
    width:28px;
    height:28px;
  }
  .dash-table {
    display: flex;
    margin-bottom: 10px;
    border:1px solid darken($base-light-color, 12%);
    border-radius:8px;
    overflow: hidden;
    ul {
      min-width:100px;
      flex:1 auto;
      display: inherit;
      flex-direction: column;
      &:last-child {
        li {
          border-right: none;
        }
      }

      li {
        display: inherit;
        justify-content: center;
        align-items: center;
        border-right:1px solid darken($base-light-color, 12%);
        border-bottom:1px solid darken($base-light-color, 12%);
        box-sizing: border-box;
        height: 35px;

        &:last-child {
          border-bottom: none;
        }
        &.header {
          height:35px;
          background: $base-light-color;
        }

        &.yesterday,
        &.today {
          padding:10px 0;
          font-size:1.4rem;
        }

        &.yesterday {
          font-size:1.3rem;
          color: lighten($base-dark-color, 25%);
        }

        &.today {
          font-weight:600;
        }

      }
    }
  }

}
.mid-content {
  font-size:1.1rem;
  padding:5px 0 20px 0;

  .el-col {
     display: flex;
     justify-content: center;
     align-items: center;

     span {
      display: inline-block;
      margin-left:10px;
      font-size:1.6rem;
      font-weight:600;
    }
  }

}
.table-header {
  display: flex;
  align-items: center;
  padding: 5px 0 15px 0;
  border-bottom: 1px solid $base-light-color;

  .last-uppdate-time{
    display: inline-block;
    margin-left:8px;
    font-size:.9rem;
    color:$base-gray-color
  }

  i {
    font-size:1.5rem;
    margin-right:5px;
  }

  span {
    font-size:1.1rem;
    font-weight:600;
  }

}
.stat-num-container{
    margin:0.5rem auto ;
    .liner-blue{
      background:linear-gradient(-35deg,rgba(105,211,255,1),rgba(23,165,247,1));
    }
    .liner-green{
      background:linear-gradient(-35deg,rgba(61,241,235,1),rgba(53,213,208,1));
    }
    .liner-orange{
      background:linear-gradient(-35deg,rgba(253,201,122,1),rgba(236,163,47,1));
    }
    .liner-lightgreen{
      background:linear-gradient(-35deg,rgba(163,245,122,1),rgba(89,202,34,1));
    }
    .liner-paper{
      background:linear-gradient(-35deg,rgba(105,137,255,1),rgba(97,42,221,1));
    }
    .liner-red{
      background:linear-gradient(-35deg,rgba(245,149,122,1),rgba(240,107,73,1));
    }
    .stat-num{
      height:100px;
      border-radius:10px;
      p{
        color:white ;
        font-weight:300;
      }
      p:nth-child(1){
        padding:10px 0px 0 10px;
        font-size:18px;
        font-weight:bold;
      }
      p:nth-child(2){
        font-size:24px;
        text-align: center;
      }
    }
}

.chart-container{
  height: 400px ;
  // width:600px;
  .chart-title{
    font-size:16px;
    font-weight:bold;
    color:rgba(51,51,51,1);
    line-height: 41px;
  }
}
.echarts{
  width: 100% ;
  height: 300px;
}
</style>



