import http from '@/utils/http'
/**
 * DASHBOARD API
 */

/**
 * 疲劳检测当日/昨日信息
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getWarnsCount () {
  return http.post(
    '/homePage/warns/count/get'
  )
}

/**
 * 设备状态列表
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getDeviceStateList (params) {
  return http.post(
    '/homePage/devices/status/get',
    params
  )
}

/**
 * 全部库位使用情况
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getArea (params) {
  return http.post(
    '/warehouse/shelves/areaAll',
    params
  )
}

/**
 * 库位使用情况
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getShelves (params) {
  return http.post(
    '/warehouse/shelves/areaEach',
    params
  )
}


/**
 * 获取首页统计数据
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getStatData (params) {
  return http.post(
    '/wms/stat/getWaitHandleOrderCount',
    params
  )
}
/**
 * 货物积压时间(超过三个月的货物) 甘特图
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getGanttData (params) {
  return http.post(
    '/wms/stat/goodBacklog',
    params
  )
}
/**
 * 待完成任务汇总 柱状图
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getBarData (params) {
  return http.post(
    '/wms/stat/pendingTaskSummary',
    params
  )
}
/**
 * 不同商家的货物总数 饼状图
 *
 * @export
 * @param {any} params
 * @returns
 */
export function getPieData (params) {
  return http.post(
    '/wms/stat/getMerchantTotalProduct',
    params
  )
}



