<template>
  <chart :options="option" ref="echartsWaybill"/>
</template>

<script>
  import CommonWrapper from '@/components/commonWrapper'
  import ECharts from 'vue-echarts/components/ECharts'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/chart/pie'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title'
  import 'echarts/lib/component/dataZoom'
  import moment from 'moment'

  export default {
    name: 'echartsWaybill',
    mounted () {
          this.selfAdaption ()
    },
    data () {
      return {
        endDate:[],
        startDate:[],
        option: {
            color: ['#3EAFF7','#F5B366','#4FDAD2','#F3D38C','#C9F0F4'],
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                },
                formatter:  (params) => {
                  // console.log(params)
                  //   // return '积压时间:' +  Number(params[1].value - params[0].value)   + '天' ;
                  //   console.log(new Date(params[1].value),"111",new Date(params[0].value))
                  //   console.log(this,startDate,this.endDate,params[1].value,params[0].value)
                  //   console.log(moment(new Date(params[1].value)).diff(new Date(params[0].value),'days',true))
                  //   console.log("2",Number(moment(new Date(params[1].value)).diff(new Date(params[0].value),'days',true)).toFixed(2))
                  //   return params[1].name + '</br>' + '积压时间:' + (Number(moment(new Date(this.startDate[params[1].dataIndex])).diff(new Date( this.startDate[params[0].dataIndex]),'days',true))).toFixed(2)  + '天' ;
                }
            },
            legend: {
                data:['收入']
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            yAxis: {
                type : 'category',
                show:false,
                splitLine: {show:false},
                data :  []
            },
            xAxis: {
                type : 'value',
                show:false,
                // axisLabel: {
                //         formatter:(val)=>{
                //           console.log(val,Number(moment(val).diff(new Date(),'days',true)).toFixed(2) + '天')
                //           return Number(moment(val).diff(new Date(),'days',true)).toFixed(2) + '天'
                //         }
                // }
            },
            dataZoom: [
                  {
                    show:false,
                    id: 'dataZoomY',
                    type: 'slider',
                    left:'85',
                    yAxisIndex: [0],
                    filterMode: 'filter'
                }
            ],
            series: [
                {
                    name: '辅助',
                    type: 'bar',
                    stack: '总量',
                    barMaxWidth:'30',
                    itemStyle: {
                        normal: {
                            barBorderColor: 'rgba(0,0,0,0)',
                            color: 'rgba(0,0,0,0)'
                        },
                        emphasis: {
                            barBorderColor: 'rgba(0,0,0,0)',
                            color: 'rgba(0,0,0,0)'
                        }
                    },
                    label: {
                      normal: {
                          show: true,
                          position: ['70%', '30%'],
                          color:'red'
                      }
                    },
                    data:[0]
                    // data: [0, 900, 1245, 1530, 1376, 1376, 1511, 1689, 1856, 1495, 1292]
                },
                {
                    name: '积压时间',
                    type: 'bar',
                    stack: '总量',
                    barMaxWidth:'30',
                    color: '#3EAFF7',
                    label: {
                        normal: {
                            show: true,
                            position: 'right'
                        }
                    },
                    data:[900]
                    // data: [900, 345, 393, '-', '-', 135, 178, 286, '-', '-', '-']
                }
            ]
        }
      }
    },
    props: {
      getSeries: {
        type: Array
      }
    },
    watch: {
      getSeries(newVal) {
        if(newVal && newVal.length > 0){
          this.option.legend.data = []
          this.option.series[0].data = []
          this.option.series[1].data = []
          this.option.yAxis.data = []
          this.endDate = []
          this.startDate = []
          // this.option.xAxis.data = []
          // this.option.xAxis.show = true
          this.option.yAxis.show = true
          if(newVal.length >= 6){
            this.option.dataZoom[0].show = true ;
          }else{
            this.option.dataZoom[0].show = false ;
          }
          newVal.map(item =>{
            this.option.yAxis.data.push(item.productName)
            this.option.series[0].data.push(item.receiveDate ? moment(item.receiveDate).format('YYYYMMDD') : moment(new Date()).format('YYYYMMDD'))
            this.startDate.push(item.receiveDate ? moment(item.receiveDate).format('YYYY-MM-DD') : moment(new Date()).format('YYYY-MM-DD'))
            this.endDate.push(item.endDate ? moment(item.endDate).format('YYYY-MM-DD') : moment(new Date()).format('YYYY-MM-DD'))
            this.option.series[1].data.push(item.endDate ? moment(item.endDate).format('YYYYMMDD') : moment(new Date()).format('YYYYMMDD'))
            // this.option.series[0].data.push(item.receiveDate ? moment(item.receiveDate).unix() : moment(new Date()).unix())
            // this.option.series[1].data.push(item.endDate ? moment(item.endDate).unix() : moment(new Date()).unix() )
            // this.option.xAxis.data.push(parseInt(moment(item.endDate ? item.endDate : new Date()  ).diff(item.receiveDate,'days',true)))
          })
          // this.option.xAxis.data = [...this.option.series[0].data,...this.option.series[1].data]
          // this.option.xAxis.data = [20181018,20181205]
          console.log(this.option.series[0].data,this.option.series[1].data)
          this.option.tooltip.formatter = (params) =>{
            console.log("1111",moment(this.endDate[0]).diff(moment(this.startDate[0]), 'days'))
            console.log(this.startDate,this.endDate, (moment(new Date(this.startDate[params[1].dataIndex])).diff(new Date( this.startDate[params[0].dataIndex]),'days',true)) )
              return params[1].name + '</br>' + '积压时间:' + moment(this.endDate[params[1].dataIndex]).diff(moment(this.startDate[params[0].dataIndex]), 'days')  + '天' ;
          }
        }else{
          this.option.series[0].data = []
          this.option.series[1].data = []
          this.option.yAxis.data = []
          // this.option.xAxis.data = []
          // this.option.xAxis.show = false
          this.endDate = []
          this.startDate = []
          this.option.yAxis.show = false
          this.option.dataZoom[0].show = false ;
        }
      }
    },
    components: { CommonWrapper, chart: ECharts },
    methods:{
      selfAdaption ()  {
        let _this = this ;
        setTimeout(() => {
            window.addEventListener('resize', function () {
                _this.$refs.echartsWaybill.resize();
            })
          }, 10)
      }
    }
  }
</script>

<style scoped>

</style>



